import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/css/reset.css'
import './assets/js/rem'

Vue.config.productionTip = false 
global.API_PROXY='https://bird.ioliu.cn/v1?url='//创建全局变量API_PROXY,地址是远程代理接口地址

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



