<template>
    <div>
        <common-hback>
            <h1 slot="title">演出详情</h1> 
        </common-hback>
        <div class="main-sDetails">
            <section class="sec1">
                <dl>
                    <dt><img :src="show.src" alt=""></dt>
                    <dd>
                        <h2>{{show.nm}}</h2>
                        <div>{{show.money}}元 
                            <span>预售</span>
                            <img src="../../assets/img/like.jpg" alt="">
                        </div>
                    </dd>
                </dl>
            </section>

            <section class="sec2">
                <p><img src="../../assets/img/time.jpg" alt="">{{show.time}}</p>
                <p>
                    <img src="../../assets/img/addr.jpg" alt="">
                    <span>{{show.addr}}</span>
                    <span>复兴路69号</span> 
                    <span>&gt;</span>
                </p>
            </section>

            <section class="sec-reset">
                <dl>
                    <dt>演出详情</dt>
                    <dd>
                         <img src="../../assets/img/more1.jpg" alt="">
                         <p>奉上座位图，考验手速的时候到了~</p>
                         <img src="../../assets/img/more2.jpg" alt="">
                         <p>自2002年3月中开始举行的“音乐之旅世界巡回演唱会”，张学友走遍世界各地巡回表演，演出地点包括马来西亚、新加坡、美加、印尼、泰国以及中国大陆等/p>
                         <img src="../../assets/img/more3.jpg" alt="">
                         <p>并于2003年1月16日起一连12场在香港红馆举行“张学友音乐之旅－压轴篇”。此三CD集收录了他于最后一场香港站的精彩演出！</p>
                         <img src="../../assets/img/more4.jpg" alt="">
                         <p>2003年6月12日发行演唱会香港场现场录音CD《音乐之旅Live演唱会》，10月22日发行现场录影VCD，10月30日发行DVD。</p>
                         <img src="../../assets/img/more5.jpg" alt="">
                         <p>虽然“音乐之旅”演唱会已唱了接近一年，但学友一直力臻完美，务求每次在舞台上展现他最精彩的演出。</p>
                         <img src="../../assets/img/more6.jpg" alt="">
                         <p>由于是次大会主题是“音乐之旅”为题，学友希望能做个有品味及以音乐为主的演唱会 </p>
                         <img src="../../assets/img/more7.jpg" alt="">
                         <p>因此舞台以美国百老汇形式设计，着重音响和灯光效果；而最特别的是在伴奏方面</p>
                         <img src="../../assets/img/more8.jpg" alt="">
                         <p>学友选用了不少小提琴及印度敲击乐等乐器，这是在流行音乐会上都较少出现。</p>
                         <img src="../../assets/img/more9.jpg" alt="">
                         <p>温柔如水的情歌“李香兰”、“每天爱你多一些”，还是快歌“饿狼传说”、“忘记他”等38首脍炙人口的歌曲，此白金豪华版《音乐之旅Live 演唱会》为你一一呈上！</p>
                         <img src="../../assets/img/more10.jpg" alt="">
                        <p>学友先以穿上黑色“踢死兔”出场，然后再以结他自弹自唱，以四首英文歌揭开序幕；他一边唱人歌，一边挥动咪柱，引来台下Fans尖声狂叫，气氛相当热闹。之后他亦大唱了不少耳熟能详的歌曲，令他的乐迷听得如痴如醉。</p>
                    </dd>
                </dl>
            </section>

            <section class="sec-reset">
                <dl>
                    <dt>购票须知</dt>
                    <dd>
                        <img src="../../assets/img/shop.jpg" alt="">
                    </dd>
                </dl>
                
            </section>
        </div>
        <div class="foot-sDetails">
            <h1>立即预定</h1>
        </div>
    </div>
</template>

<script>
import commonHback from '../common/HeaderBack'
import Axios from 'axios'
export default{
    data(){
        return{
            show:[],
        }
    },
    components:{
        commonHback,
    },
    mounted(){
         let url1 = './static/data/showdetails1.json'
            Axios.get(url1)
            .then(res=>{
                this.show = res.data.showdetails[0];
                // console.log(this.show)
            }).catch(
                ()=>{alert("details-show数据库连接失败")}
            )
    }
}
</script>

<style>
    .main-sDetails{
        margin: 1rem 0;
        width: 100%;
        height: 100%;
        position: relative;
        background: #F3f3f3;

    }
    .main-sDetails section{
        margin-bottom: 0.25rem;
        background: #fff;
        padding: 0.2rem;
    }
    .foot-sDetails{
        height: 1rem;
        width: 100%;
        background: rgb(196, 27, 27);
        z-index: 9;
        position: fixed;
        bottom: 0;
        left: 0;
    }
    .foot-sDetails h1{
        font: 0.35rem/1rem "微软雅黑";
        text-align: center;
        font-weight: bold;
        color:#fff;
    }
    .sec1 dl{
        display: flex;
    }
    .sec1 dl dt{
        width: 30%;
    }
    .sec1 dl dd{
        padding-left: 0.3rem;
    }
    .sec1 dl dd h2{
        font:0.34rem/0.5rem "微软雅黑";
        font-weight: bold;
    }
    .sec1 dl dd div{
        font:0.33rem/1rem "微软雅黑";
        font-weight: bold;
        color:#f00;
        position: relative;
        top:0.3rem;
    }
    .sec1 dl dd div span{
        color:orange;
        border:1px solid orange;
        font: 0.22rem/0.22rem "微软雅黑";
    }
    .sec1 dl dd div img{
        position: absolute;
        right:0;
        bottom:0.3rem;
    }
    .sec2 p:nth-of-type(1){
        font:0.25rem/0.7rem "微软雅黑";
        border-bottom:1px solid #eeeeee;
        
    }
    .sec2 p:nth-of-type(1) img{
        position: relative;
        top: 0.2rem;
        padding-right: 0.1rem;
    }
    .sec2 p:nth-of-type(2){
        position: relative;
    }
    .sec2 p:nth-of-type(2) img{
        position: absolute;
        top: 0.1rem;
        left: 0;
    }
    .sec2 p:nth-of-type(2) span{
        display:block;
        margin: 0.1rem 0 0 0.4rem;
    }
    .sec2 p:nth-of-type(2) span:nth-of-type(2){
        font: 0.2rem/0.2rem "微软雅黑";
        color:rgb(201, 198, 198);
    }
    .sec2 p:nth-of-type(2) span:nth-of-type(3){
        position: absolute;
        right: 0.1rem;
        bottom: 0.1rem;
        font-size:0.35rem;
        font-weight: bold;
        color:#999;
        font-family: "微软雅黑";
    }
    .sec-reset{
    }
    .sec-reset dl dt{
        font:0.29rem/0.5rem "微软雅黑";
        color:#333;
        font-weight: bold;
        border-bottom:1px solid #eee;
        padding-bottom:0.2rem;
    }
    .sec-reset dl dd{
        width: 100%;
        overflow: hidden;
    }
    .sec-reset dl dd img{
        width: 100%;
        margin-left: -0.02rem;
    }
    .sec-reset dl dd p{
        padding-top: 0.1rem;
        font:0.25rem/0.4rem "微软雅黑";
    }
</style>


