<template>
    <div class="header">
        <ul>
            <li class="addr" @click="goNext">北京 ▼</li>
            <li><h1 @click='goHome'><slot name='title'>猫眼电影</slot></h1>
            <li class="search" @click="goSearch"><img src="../../assets/img/search2.jpg" alt=""></li>
        </ul>
    </div>
</template>

<script>
export default{
    methods:{
        goHome(){
            this.$router.push('/')
        },
        goNext(){
            this.$router.push('/others/addr')
        },
        goSearch(){
            this.$router.push('/others/search')
        }
    }
}
    
</script>
<style>
    .header{
        position: fixed;
        top: 0;
        left: 0;
        height: 1rem;
        width: 100%;
        background: #E54847;
        z-index: 9;
    }
    .header ul{
        display: flex;
    }
    .header li{
        flex-grow:3;
        line-height: 1rem;
        text-align: center;
        color:#fff;
    }
    .header li h1{
        font: 0.4rem/1rem "微软雅黑";
        text-align: center;
        padding-left: 0.3rem;
    }
    .header li.search img{
        height: 45%;
        padding: 0.3rem 0 0 0.5rem;
    }
</style>




