<template>
    <div class="list-main">
       <nav class="on">
           <ul>
               <li><router-link to="/movie/movielist/movielistnow">正在热映</router-link></li>
               <li><router-link to="/movie/movielist/movielistfuture">即将上映</router-link></li>
           </ul>
       </nav>
       <router-view></router-view>
    </div>
    
</template>


<script>

export default {
    mounted(){
        
    }
}
</script>

<style>
    .list-main{
        margin: 1rem 0;
        height: 800px;
    }
    .list-main .on{
        width: 100%;
        background: #FFFFFF;
        border:1px solid #E6E6E6;
        height: 0.8rem;
        position: fixed;
        top:1rem;
        left: 0;
        z-index: 9;
    }
    .list-main .on ul{
        display: flex;
    }
    .list-main .on ul li{
        flex-grow: 1;
        text-align: center;
        
    }
    .list-main .on ul li a{
        color:#666666;
        font:0.3rem/0.8rem "微软雅黑";
        font-weight: bold;
        padding: 0 0.1rem 0.2rem;
    }
    .list-main .on a.router-link-active{
        border:none;
        border-bottom:2px solid #EF4238;
        color:#EF4238;
    }
</style>



