<template>
    <div>
        <common-header><h1 slot="title">演出</h1></common-header>
            <div class="main-show">
                <section class="banner"  @click="goFor">
                    <swipe class="show-swipe">
                        <swipe-item class="slide"><img src="../../assets/img/show-banner2.jpg" alt=""></swipe-item>
                        <swipe-item class="slide"><img src="../../assets/img/show-banner1.jpg" alt=""></swipe-item>
                        <swipe-item class="slide"><img src="../../assets/img/show-banner3.jpg" alt=""></swipe-item>
                        <swipe-item class="slide"><img src="../../assets/img/show-banner4.jpg" alt=""></swipe-item>
                        <swipe-item class="slide"><img src="../../assets/img/show-banner5.jpg" alt=""></swipe-item>
                        <swipe-item class="slide"><img src="../../assets/img/show-banner6.jpg" alt=""></swipe-item>
                        <swipe-item class="slide"><img src="../../assets/img/show-banner7.jpg" alt=""></swipe-item>
                    </swipe>
                </section>

                <section class="list-nav">
                    <ul>
                        <li v-for="nav in showList" :key="nav.id" @click="goFor">
                            <img :src="nav.src" alt="">
                             <p>{{nav.pro}}</p>
                        </li>
                    </ul>
                </section>

                <section class="show-1">
                    <img src="../../assets/img/show1.jpg" alt="" @click="goFor">
                </section>

                <section class="show-find">
                    <dl>
                        <dt>- 发现精彩 -</dt>
                        <dd>
                            <ul class="big-find">
                                <li  @click="goFor">
                                    <img src="../../assets/img/show-find1.jpg" alt="">
                                    <div>
                                        <h5>袁维娅</h5>
                                        <p>自然而然生日音乐会</p>
                                    </div>
                                </li>
                                <li @click="goFor">
                                    <img src="../../assets/img/show-find2.jpg" alt="">
                                    <div>
                                        <h5>羽泉20周年演唱会</h5>
                                        <p>北京特别版</p>
                                    </div>
                                </li>
                                <li @click="goFor">
                                    <img src="../../assets/img/show-find2.jpg" alt="">
                                    <div>
                                        <h5>羽泉20周年演唱会</h5>
                                        <p>北京特别版</p>
                                    </div>
                                </li>
                                 <li @click="goFor">
                                    <img src="../../assets/img/show-find1.jpg" alt="">
                                    <div>
                                        <h5>袁维娅</h5>
                                        <p>自然而然生日音乐会</p>
                                    </div>
                                </li>
                            </ul>
                        </dd>
                    </dl>
                </section>

                <section class="show-recommended">
                    <dl>
                        <dt>- 为你推荐 -</dt>
                        <dd>
                            <nav class="show-nav"> 
                                <ul>
                                    <li>当前热门</li>
                                    <li>最新上架</li>
                                    <li>折扣演出</li>
                                    <li class="nav-one">全部时间▼
                                        <dl class="nav-er">
                                            <dt>全部时间</dt>
                                            <dd>今天</dd>
                                            <dd>明天</dd>
                                            <dd>本周末</dd>
                                            <dd>一周内</dd>
                                            <dd>一个月内</dd>
                                            <dd class="mb"></dd>
                                        </dl>
                                    </li>
                                </ul>
                            </nav>
                            <ul class="show-panel">
                                <li v-for="list in showDetails" :key="list.id" @click="goFor">
                                    <div class="show-img"><img :src="list.src" alt=""></div>
                                    <div class="show-info">
                                        <h5>{{list.nm}}</h5>
                                        <p><img src="../../assets/img/time.jpg" alt="">{{list.time}}</p>
                                        <p><img src="../../assets/img/addr.jpg" alt="">{{list.addr}}</p>
                                        <p>{{list.money}}元<span>自营</span></p>
                                    </div>
                                </li>
                            </ul>
                        </dd>
                    </dl>
                </section>
            </div>             
        <common-footer></common-footer>
    </div>
</template>
<script>
import commonHeader from '../common/Header'
import commonFooter from '../common/Footer'
import Axios from 'axios'
import 'vue-swipe/dist/vue-swipe.css'
import {Swipe, SwipeItem} from 'vue-swipe'
export default {
    data(){
        return{
            showList:[],
            showDetails:[],
        }
    },
    components:{
        commonHeader,
        commonFooter,
        Swipe,
        SwipeItem,
    },
    methods:{
        goFor(){
            this.$router.push('/others/detailsshow')
        }
    },
    mounted(){
        // let url1 = "/static/data/showlist.json"
        // Axios.get(url1).then(res=>{
        //     this.showlist = res.data.list;
        //     console.log(this.showlist[0])
        // }).catch(
        //     () =>{alert("数据库连接失败")}
        // )
        //nav列表
        let url1 = './static/data/showlist.json'
        Axios.get(url1)
        .then(res=>{
            this.showList = res.data.showlist;
        }).catch(
            ()=>{alert("show-数据库连接失败")}
        )

        


        let oLi = document.getElementsByClassName("show-nav")[0].getElementsByTagName("li");
        let oLast = document.getElementsByClassName("nav-er")[0];
        for(let i=0;i<oLi.length;i++){
            //显示列表
            let url2 = './static/data/showdetails1.json'
            Axios.get(url2)
            .then(res=>{
                this.showDetails = res.data.showdetails;
            }).catch(
                ()=>{alert("-show2-数据库连接失败")}
            )

            oLi[i].onclick = () =>{
                for(let j=0;j<oLi.length;j++){
                    oLi[j].style.color="#333";
                }
                oLi[i].style.color="#f00";
                //点击 使id+1
                let num=i+1;
                let url2 =  './static/data/showdetails'+num+'.json'
                    Axios.get(url2)
                .then(res=>{
                    this.showDetails = res.data.showdetails;
                }).catch(
                    ()=>{alert("-show3-数据库连接失败")}
                )
                // $(
                //     $(".nav-one").click(
                //         function(){
                //             $(".nav-er").toggle();
                //         }
                //     ),
            
                // )
                if(i==3){
                    oLast.style.display="block";
                }else{
                    oLast.style.display="none";
                }
                
            }
        }
    }
}
</script>

<style>
    .main-show{
        margin: 1rem 0;
    }
    .show-swipe{
    height:1.8rem;
    color: #fff;
    text-align: center;
    }
    .slide {
        color: #fff;
    }
    .slide img{
        
        height:1.8rem;
    }
