    color: #fff;
    } */
    .list-nav{
        padding:0.2rem;
    }
    .list-nav ul{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        text-align: center;
    }
    .list-nav ul li{
        width: 20%;
        font: 0.25rem/0.7rem "微软雅黑";
    }
    .list-nav ul li img{
        width: 80%;
    }
    .show-find>dl>dt,.show-recommended>dl>dt{
        text-align: center;
        font:0.3rem/1rem "微软雅黑";
        font-weight: bold;
        color:#333;
        position: relative;
    }
    .show-find dl dt dl{
        color:#333;
    }
    .show-find ul{
        display: flex;
        justify-content: space-around;
        padding: 0.2rem;
        flex-wrap: wrap;
    }
    .show-find ul.big-find li{
        width: 48%;
        position: relative;
        margin-bottom: 0.2rem;
    }
    .show-find ul.big-find li div{
        position: absolute;
        bottom:0.2rem;
        left:0;

    }
    .show-find .big-find li div h5{
        font:0.3rem/0.3rem "微软雅黑";
        color:#fff;
        background: rgba(0,0,0,.8);
        display: inline;
        padding: 0.1rem
    }
    .show-find .big-find li div p{
        color:#333;
        font: 0.22rem/0.22rem "微软雅黑";
        padding:0.03rem 0.05rem;
        background: rgb(236, 165, 33);
    }
    .show-find .big-find li:nth-of-type(2) div p,
    .show-find .big-find li:nth-of-type(3) div p{
        width: 1.2rem;
    }
    .show-recommended dl dd nav ul{
        display: flex;
        justify-content: space-around;
        border-top:1px solid #ccc;
        border-bottom:1px solid #ccc;
        font:0.25rem/0.8rem "微软雅黑";
        position: relative;
    }
    .show-recommended dl dd nav ul li{
        color:#333;
    }
    .nav-er{
        position: absolute;
        top:0.7rem;
        right: 0;
        width: 100%;
        background: #fff;
        display: none;
    }
    .show-recommended dl dd nav ul li:nth-of-type(1){
        color:#f00;
    }
    .nav-er dt,.nav-er dd{
        border-top: 1px solid #ccc;
        padding-left: 0.2rem;
        color:#333;
    }
    .nav-er dt:hover,.nav-er dd:hover{
        color:#f00;
    } 
    .show-panel{
        width: 100%;
    }
    .show-panel li{
        overflow: hidden;
        padding:0.3rem 0.2rem;
        
    }
    .show-panel li div{
        float: left;
    }
    .show-panel li .show-img{
        width:30%;
    }
    .show-panel li .show-img img{
        width: 100%;
    }
    .show-panel li .show-info{
        width: 65%;
        float: right;
    }
    .show-panel li .show-info h5{
        font:1.2em/1.3em "微软雅黑";
        font-weight: bold;
        height: 2.6em;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .show-panel li .show-info p{
        font:0.22rem/0.5rem "微软雅黑";
        color:#666;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }
    .show-panel li .show-info p img{
        margin: 0.1rem 0.15rem 0 0;
    }
    .show-panel li .show-info p:nth-of-type(3){
        font: 1.15em/2em "微软雅黑";
        color:rgb(219, 66, 66);
        font-weight: bold;
    }
    .show-panel li .show-info p:nth-of-type(3) span{
        font:0.2rem/0.3rem "微软雅黑";
        border:1px solid rgb(253, 164, 164);
        padding:0.05rem;
        background: rgb(245, 217, 217);
        margin: 0 0 0 0.4rem;
    }
</style>



