<script>
import commonHeader from '../common/Header'
import commonFooter from '../common/Footer'
import Axios from 'axios'
import 'vue-swipe/dist/vue-swipe.css'
import {Swipe, SwipeItem} from 'vue-swipe'
export default {
    data(){
        return{
            movieList:[],//正在热映
            movieList2:[],//电视剧--正在热映
            movieDetails:[],//即将上映
            movieInfo:[],//电影资讯1
            movieVideo:[],//电影资讯2--视频
        }
    },
    components:{
        commonHeader,
        commonFooter,
        Swipe,
        SwipeItem,
    },
    methods:{
        goHome(){
            this.$router.push('/')
        },
        goNext(){
            this.$router.push('/others/addr')
        },
        goSearch(){
            this.$router.push('/others/search')
        },
        goFor(){
            this.$router.push('/movie/movielist/movielistnow')
        },
        goTo(){
            this.$router.push('/others/firstdetails')
        }
    },
    mounted(){
        //一、点击切换，
        let oLi=document.getElementsByClassName("nav-index")[0].getElementsByTagName("li");
        let oPanel=document.getElementsByClassName("panel");
        for(let i=0;i<oLi.length;i++){
            
            oLi[i].onclick = () =>{
                for(let j=0;j<oLi.length;j++){
                    oLi[j].className="";
                    oPanel[j].style.display="none";
                }
                oLi[i].className="select";
                oPanel[i].style.display="block";
                //----------------电影

            }
        };
        //页面滑动，导航定位到顶部
        let oBanner = document.getElementsByClassName("banner")[0];
        let oNav = document.getElementsByClassName("nav-index")[0];
        window.onscroll = ()=>{
            /*导航滚动过后定位在顶部*/
            if(document.documentElement.scrollTop>oNav.offsetHeight+10){
                oNav.style.position="fixed";
                oNav.style.top="1rem";
                oNav.style.left="0rem";
            }else{
                oNav.style.position="relative";
                oNav.style.top="0";
                oNav.style.left="0";
            }  
        }
        //----------------电影
        //1.正在热映
        let url1='./static/data/moviedata.json'
         Axios.get(url1).then(res=>{
            this.movieList=res.data.data.movies;
         }).catch(
             ()=>{alert("index-正在热映-数据库连接失败")}
         )
        //2.即将上映
        let url2='./static/data/moviefuturedata.json'
         Axios.get(url2).then(res=>{
            this.movieDetails=res.data.data.movies;
         }).catch(
             ()=>(alert("index-即将上映-数据库连接失败"))
         )
         //3.资讯1 
         //5.资讯3--
         let url3='./static/data/information.json'
         Axios.get(url3).then(res=>{
            this.movieInfo=res.data.info;
            // console.log(res.data.info)
         }).catch(
             ()=>(alert("index-电影资讯1-数据库连接失败"))
         )
         //4.资讯2-视频
         let url4='./static/data/videodata.json'
         Axios.get(url4).then(res=>{
            this.movieVideo=res.data.video;
            // console.log(res.data.info)
         }).catch(
             ()=>(alert("index-电影资讯1-数据库连接失败"))
         )
         
    }
}
</script>

<style>
