    }
    .main-index article .recent ul li .list-img{
        position: relative;
    }
    .main-index article .recent ul li .list-img img{
        width: 100%;
        height: 2.3rem;
        background: rgba(0,0,0,.5);
    }
    .main-index article .recent ul li .list-img span{
        position: absolute;
        bottom: 0;
        left: 0;
        color:rgb(212, 155, 20);
        font:0.23rem/1.8em "微软雅黑";
        font-weight: bold;
        text-align: center;
        display: inline-block;
        width: 100%;
    }
    .main-index article .recent ul li .list-info p:nth-of-type(1){
        font: 0.3rem/0.5rem "微软雅黑";
        font-weight: bold;
        color:#222222;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        width: 100%;
    }
    .main-index article .recent ul li .list-info p:nth-of-type(2){
        font:0.22rem/0.3rem "微软雅黑";
        color:#fff;
        background: rgb(238, 36, 36);
        border-radius:0.6rem;
        display: inline;
        padding: 0.08rem 0.28rem;
    }
    .main-index article .recent:nth-of-type(2) ul{
        width: 365%;
    }
    .main-index .list-info #date{
        color: #999;
        border: none;
        background: none;
        z-index: 99;
        padding-left: 0;
        width: 100%;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .nav-panel .panel{
        display: none;
    }
    .nav-panel .panel:nth-of-type(1){
