    }
    /*.nav-panel*/
    .nav-panel{

    }
    /*电影资讯1*/
    .zixun{
        margin-top: 0.3rem;
        border-top:0.1rem solid rgb(248, 248, 248);
    }
    .zixun ul li,.zixun ol li{
        border-bottom:1px solid #eee;
        padding: 0.3rem 0.2rem;
    }
    .zixun ul li dl dt h5{
        font:0.3rem/0.45rem "微软雅黑";
        margin-bottom: 0.1rem;
    }
    .zixun ul li dl dd{
