    }
    .zixun ol li dl dd img{
        border-radius: 0.1rem;
        width: 100%;
        height: 1.3rem;
    }
    .bottom{
        text-align: center;
        font:0.25rem/0.3rem "微软雅黑";
        padding-top: 0.25rem;
    }
    /**----------------电视剧*/
    .pan2 section,.pan4 section{
        border:none;
    }
</style>



