<template>
    <div class="header">
        <ul>
            <li class="back"  @click="$router.go(-1)">&lt;</li>
            <li><h1 @click='goHome'><slot name='title'>电影</slot></h1>
            <li class="search"></li>
        </ul>
    </div>
</template>

<script>
export default{
    data(){
        return{
            movieList:[],
        }
    },
    methods:{
        goHome(){
            this.$router.push('/')
        },
    }

}
    
</script>

<style>
    .header{
        height: 1rem;
        width: 100%;
        background: #E54847;
        z-index: 9;
    }
    .header ul{
        display: flex;
    }
    .header li{
        flex-grow:3;
        line-height: 1rem;
        text-align: center;
        color:#fff;
        
    }
    .header li.back{
        position: relative;
        left: -0.5rem;
        font: 0.7rem/1rem "宋体";
        color:#fff;
    }
    .header li h1{
        font: 0.38rem/1rem "微软雅黑";
        position: relative;
        left: -0.15rem;
    }
    .header li.search img{
        height: 45%;
        padding: 0.3rem 0 0 0.5rem;
    }
</style>



