<template>
    <div>
        <common-hback><h1 slot="title">猫眼电影</h1></common-hback>
        
        <div class="my-main">
            <ul>
                <li class="select">美团账户登录</li>
                <li>手机验证登录</li>
            </ul>
            <section>
                <form action="/movie/movieList">
                    <input type="text" placeholder="帐户名/手机号/Email">
                    <input type="text" placeholder="请输入您的密码">
                    <button>登录</button>
                </form>
                <div class="login">
                    <router-link to="">立即注册</router-link>
                    <router-link to="">找回密码</router-link>
                </div>
                <p>© 猫眼电影 客服电话：<router-link to="">100-100-1000</router-link></p>
            </section>
            <section>
                <form action="/movie/movieList">
                    <input type="tel" placeholder="请输入手机号"><span>获取验证码</span>
                    <input type="text" placeholder="请输入短信验证码">
                    <button>登录</button>
                </form>
                <div class="login">
                    <router-link to="">立即注册</router-link>
                    <router-link to="">找回密码</router-link>
                </div>
                <p>© 猫眼电影 客服电话：<router-link to="">100-100-1000</router-link></p>
            </section>
        </div>                   
        <common-footer></common-footer>
    </div>
</template>
<script>
import commonHback from '../common/HeaderBack' 
import commonFooter from '../common/Footer'
export default {
    components:{
        commonHback,
        commonFooter,
    },
    mounted(){
        let oLi=document.getElementsByClassName("my-main")[0].getElementsByTagName("li");
        let oPanel=document.getElementsByClassName("my-main")[0].getElementsByTagName("section");
        for(let i=0;i<oLi.length;i++){
            
            oLi[i].onclick = () =>{
                for(let j=0;j<oLi.length;j++){
                    oLi[j].className="";
                    oPanel[j].style.display="none";
                }
                oLi[i].className="select";
                oPanel[i].style.display="block";
            }
        }
    }
}
</script>
<style>
.header{
    position: fixed;
    top: 0;
    left:0;
}
    .my-main{
        width:100%;
        margin:1rem 0;
    }
    .my-main ul{
        width: 100%;
        display: flex;
        justify-content: space-around;
        background: #fff;
        border-bottom:0.1rem solid #D6D6D6;
        height:0.8rem;
    }
    .my-main ul li{
        font: 0.25rem/0.8rem "微软雅黑";
        height:0.8rem;
        padding:0 0.7rem;
    }
    li.select{
        border-bottom:0.1rem solid #DF2D2D;
        color:#DF2D2D;
    }
    .my-main section{
        width: 100%;
    }
    .my-main section:nth-of-type(2){
        display:none;
    }
    .my-main section form{
        width: 100%;
        background: #fff;
        margin-bottom: 0.2rem;
        position: relative;
    }
    .my-main form input{
        margin-left: 2%;
        padding-left: 2%;
        border:none;
        width: 96%;
        border-bottom:0.01rem solid #EDEDED;
        font:0.28rem/0.95rem "微软雅黑";
        height:0.95rem;
    }
    .my-main form span{
        position: absolute;
        top:0.15rem;
        right:0.2rem;
        background: #ccc;
        padding:0.06rem 0.3rem;
        border-radius:0.1rem;
        color:#999999;
    }
    .my-main form button{
        width: 92%;
        background: #DF2D2D;
        color:#fff;
        font:0.38rem/0.3rem "微软雅黑";
        text-align: center;
        border:none;
        border-radius:0.1rem;
        padding: 0.32rem 0;
        margin:0.2rem 3% 0.1rem;
    }
    .my-main .login{
        position: relative;
        height:0.5rem;
    }
    .my-main .login a{
        color:#DF2D2D;
        font:0.25rem/0.3rem "微软雅黑";
    }
    .my-main .login a:nth-of-type(1){
        position: absolute;
        left:3%;
        top:0;
    }
    .my-main .login a:nth-of-type(2){
        position: absolute;
        right: 3%;
        top: 0;
    }
    .my-main p{
        text-align: center;
        color:#333333;
        font:0.27rem/0.4rem "微软雅黑";
    }
    .my-main p a{
        color:#DF2D2D;
    }
</style>



