<template>
    <div class="nav-top">
        <!--电影简介-->
        <section class="intro">
            <dl>
                <dt><img :src="movieCinema.img" alt=""></dt>
                <dd>
                    <h5>{{movieCinema.nm}}</h5>
                    <p>A coll fish</p>
                    <p><span>{{movieCinema.sc}}</span>({{movieCinema.snum}}人评价)</p>
                    <p>{{movieCinema.cat}}/{{movieCinema.dur}}</p>
                    <p>{{movieCinema.src}}/{{movieCinema.dur}}分钟</p>
                    <p>{{movieCinema.rt}}</p>
                    <div @click="goDetail()">&gt;</div>
                </dd>
            </dl>
        </section>

        <!--放映时间-->
        
        <!--导航部分-->
        <section id="tit">
            <nav id="data">
                <ul class="over_f">
                    <li>今天11月02日</li>
                    <li>明天11月03日</li>
                    <li>后天11月04日</li>
                    <li>周一11月05日</li>
                    <li>周二11月06日</li>
                    <li>周三11月07日</li>
                    <li>周四11月08日</li>
                </ul>
            </nav>
            <nav id="secondary_nav">
                    <ul>
                        <li>全城<span></span></li>
                        <li>品牌<span></span></li>
                        <li>特色<span></span></li>
                    </ul>
            </nav>
        </section>


        <section class="movie-house">
            <dl v-for="add in cinema" :key="add.title">
                <dt><a href="">{{add.title}} <b>{{add.average_fare}}</b><span>元起</span></a> </dt>
                <dd>
                    <a href="">
                        <p>{{add.dizhi}}<span>{{add.average_person_number}}km</span></p>
                        <ul class="item">
                            <li class="blue kuai">退</li>
                            <li class="blue kuai">改签</li>
                            <li class="kuai orange">小吃</li>
                            <li class="kuai orange">折扣卡</li>
                        </ul>
                        <p class="gray"><img src="images/list/card.png" alt=""/>开卡特惠，首单2张立减2元</p>
                        <ul>
                            <li class="gray" style="margin-top: 2px;">近期场次：</li>
                            <li><a href=""  class="gray">20:00</a></li>
                            <li class="gray"> | </li>
                            <li><a href=""  class="gray">21:00</a></li>
                            <li class="gray"> | </li>
                            <li><a href=""  class="gray">22:00</a></li>
                        </ul>
                    </a>
                </dd>
            </dl>
        </section>
    </div>
</template>
<script>
import Axios from 'axios'
export default{
    data(){
        return{
            movieCinema:[],
            cinema:[],
            sz:[]
        };
    },
    methods:{
         goDetail(){
             this.$router.push('/details/detailsmovie/'+this.sz);
        }
    },
    mounted(){
        //按照id获取地址
        this.sz=this.$route.params.movieId;/*接收id*/
        let url1="./static/data/moviedetail" + this.$route.params.movieId +".json";
        // let url1 = '/static/data/moviedetail248075.json'
        let url2 = './static/data/yingyuan.json'
        Axios.get(url1)
        .then(res=>{
            this.movieCinema = res.data.data.MovieDetailModel;
        }).catch(
            ()=>{alert("暂无数据，请关注前几个电影哦~")}
        )

        Axios.get(url2)
        .then(res=>{
            this.cinema = res.data.data;
            // console.log(this.cinema)
        }).catch(
            ()=>{alert("数据库连接失败")}
        )

        let oInHeight=document.getElementsByClassName("intro")[0].offsetHeight;
        let oTit=document.getElementById("tit");
        let oLi=document.getElementById("data").getElementsByTagName("li");
        for(let i=0;i<oLi.length;i++){
            oLi[i].onclick= () =>{
                for(let j=0;j<oLi.length;j++){
                    oLi[j].style.borderBottom="none";
                    oLi[j].style.color="#886565";
                }
                oLi[i].style.borderBottom="2px solid #F03D37";
                oLi[i].style.color="#F03D37";
            }
        }
        window.onscroll=() =>{ 
            if(document.documentElement.scrollTop>oInHeight){
               oTit.style.position="fixed";
               oTit.style.top="0";
               oTit.style.left="0";
            }else{
                oTit.style.position="relative";
            }
        };
    }
}
</script>
<style>
.nav-top{
    width: 100%;
    margin-top: 1rem;
}
/*隐藏横向滚动条*/
::-webkit-scrollbar{
    display: none;
}
.intro{
    width: 100%;
    background: url(../../assets/img/bg1.jpg) no-repeat;
    background-size:100%;
}
.intro dl{
    padding:0.35rem 0.3rem;
    display:flex;
    position: relative;
    
}
.intro dl dt{
    width:40%;
}
.intro dl dd{
    padding-left:0.3rem;
}
.intro dl dd h5{
    font:0.4rem/0.5rem "微软雅黑";
    color:#FFFFFF;
    font-weight: bold;
}
.intro dl dd p{
    color:#E3DEDA;
    font:0.25rem/0.48rem "微软雅黑";
    font-weight: bold;
}
.intro dl dd p span{
    color:#FFCC00;
    font-size:0.36rem;
    margin-right: 0.3rem;
}
.intro dl dd div{
    position:absolute;
    right:0.3rem;
    bottom:1.6rem;
    color:#C9BDAE;
    font:0.4rem/0.4rem "宋体";
    font-weight: bold;
}
    
#tit{
    width: 100%;
    overflow:hidden;
    background: #fff;
    z-index: 9;
}
#tit #data{
    width: 100%;
    background: #fff;
    overflow: auto;
    border-bottom: 2px solid #F0F0F0;
}
#tit #data ul{
    width: 240%;
    overflow-x: hidden;
    overflow-y: scroll;
}
#tit #data ul li{
    float: left;
    font:0.25rem/0.7rem "微软雅黑";
    text-align: center;
    margin:0 0.25rem;
    color:#886565;
}
 #tit #data ul li:first-child{
    border-bottom: 2px solid #F03D37;
    color:#F03D37;
}

#secondary_nav{
    width: 100%;
    overflow:auto;
    border-bottom: 1px solid #f0f0f0;
    background: #fff;
}
#secondary_nav ul{
    display:flex;
    justify-content: space-around;
    text-align: center;
}
#secondary_nav li{
    border-right: 1px solid #f0f0f0;
    color:#878787;
    font:0.3rem/0.8rem "微软雅黑";
    padding:0 0.5rem;
}
#secondary_nav li span{
    display: inline-block;
    margin-left: 4px;
    border: 4px solid transparent;
    border-top-color: #b0b0b0;
}
/*按钮被点击状态*/
#secondary_nav li:hover{
    color:#f00;
}
#secondary_nav li:hover span{
    display: inline-block;
    margin-left: 4px;
    border: 4px solid transparent;
    border-bottom-color: #f00;
}
#secondary_nav li:last-child{
    border:none;
}

/*content内容部分*/
.movie-house{
    width: 97%;
    position: relative;
    left: 3%;
    top:0; 
}
.movie-house dl{
    border-bottom: 1px solid #F0F0F0;
    padding: 0.2rem 5% 0.2rem 0px;
}
.movie-house dl dt{
    font:1.1em/1.5em "微软雅黑";
}
.movie-house dl dt a{
    color:#000;
}
.movie-house dl dt a b{
    color:#F03D37;
    margin-left: 5px;
}
.movie-house dl dt span{
    color:#F03D37;
    font:0.7em/1em "微软雅黑";
}
.movie-house dl dd ul{
     overflow: hidden;
 }
.movie-house dl dd ul li{
    float: left;
}
.movie-house dl dd p:nth-of-type(1){
    color:#9A9A9A;
    font:0.85em/2em "微软雅黑";
    position: relative;
}
.movie-house dl dd p:nth-of-type(1) span{
    position: absolute;
    right: 0px;
}
.movie-house dl dd ul .blue{
    color: #589daf;
    border: 1px solid #589daf;
}
.movie-house dl dd ul .orange{
    color: #f90;
    border: 1px solid #f90;
}
.movie-house dl dd ul .kuai{
    display: inline-block;
    height: 20px;
    line-height: 18px;
    padding: 0px 3px;
    text-align: center;
    border-radius: 2px;
    font-size: 0.2rem;
}
.movie-house dl dd ul:nth-of-type(1) li{
    margin-right: 5px;
}
.movie-house dl dd .gray{
    color:#C0C0C0;
    font:0.8em/1.7em "微软雅黑";
}
.movie-house dl dd p:nth-of-type(2){
    margin-top: 5px;
}
.movie-house dl dd p img{
    height: 15px;
    position: relative;
    top:3px;
    margin-right: 5px;
}
</style>


