<template>
    <div>
        <h2>
            <ul class="now-list">
                <li v-for="movie in movieList" :key="movie.id" @click='goDetail(movie.id)'>  <!-- @click='goDetail' -->
                   <!--跳转到详情页-->
                        <div class="movie-img"><img :src="movie.img" alt="电影图片"></div>
                        <div class="movie-info">
                            <h5>{{movie.nm}}</h5>
                            <p>观众评<span>{{movie.sc}}</span></p>
                            <p>主演：{{movie.star}}</p>
                            <p>{{movie.showInfo}}</p>
                            <router-link to="">购票</router-link>
                        </div>
                </li>
                <div class='gif' v-show="loadingShow">
                    <img src="../../assets/img/loading.gif" alt="">
                </div>
                <div class='tip' v-show="tip">
                    <p>--------- 敬请期待，其他好剧 ---------</p>
                </div>
            </ul>
             
        </h2>
    </div>
</template>

<script>


import Axios from "axios";
export default {
    data(){
        return{
            movieList:[],
            // loadingShow:false,
            // tip:false
        };
    },
    methods:{
        //  loaDate(){
        //  let url1='./static/data/moviedata.json'
        // //  let url1='/static/data/moviedata.json'

        // Axios.get(url1).then(res=>{
        //     let list=res.data.data.movies;
        //     let data=list.slice(this.movieList.length,this.movieList.length+5);
        //     if(data.length<3){
        //         this.tip=true;
        //     }
        //      this.movieList=this.movieList.concat(data);
        //     //  console.log(this.movieList);
        //     }).catch(
        //         ()=>{alert('movienow-数据错误')}
        //         );   
        // },
        goDetail(movieId){
            //id传
            this.$router.push('/details/detailscinema/'+movieId);
           /*  this.$router.push('/details/detailscinema/'); */
        }
    },
    mounted(){
        // this.loaDate();
        
         let url1='./static/data/moviedata.json'
        //  let url1='/static/data/moviedata.json'

        Axios.get(url1).then(res=>{
            this.movieList=res.data.data.movies;
        }).catch(
            ()=>{alert("即将上映--数据库连接失败")}
        )

        // window.onscroll=()=>{
        //     let clientHeight= document.documentElement.clientHeight;//可视区域高度
        //     let scrollTop=document.documentElement.scrollTop;//滚动条的上方的像素点
        //     let scrollHeight=document.documentElement.scrollHeight;//能滚动高度
        //     if(clientHeight+scrollTop==scrollHeight){
        //         this.loadingShow=true;
        //         if(!this.tip){
        //             this.loaDate();
        //         }else{
        //             this.loadingShow=false;
        //         }
        //     }
        // }
    }
};
</script>

<style>
    .now-list{
        margin: 1.8rem 0 1rem 0;
        width: 100%;
        
        overflow: hidden;

    }
    .now-list li{
        padding:0.2rem 0.3rem;
        display: flex;
    }
    .now-list li .movie-img{
        width: 1.5rem;
    }
    .now-list li .movie-img img{
        width: 100%;
    }
    .now-list li .movie-info{
        width: 70%;
        border-bottom: 1px solid #F0F0F0;
        font:0.2rem/0.35rem "微软雅黑";
        padding-left:0.3rem;
        position: relative;
    }
    .now-list li .movie-info h5{
        font:0.33rem/1.5em "微软雅黑";
        font-weight: bold;
        color:#333333;
        width:70%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;

    }
    .now-list li .movie-info p{
        font:0.25rem/0.48rem "微软雅黑";
        color:#666666;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 75%;

    }
    .now-list li .movie-info p span{
        font-weight: bold;
        color:#FAAF00;
        font-size: 0.3rem;
        margin-left: 0.15rem;
    }
    .now-list li .movie-info a{
        font-size: 0.25rem;
        background: #F03D37;
        color:#FFFFFF;
        padding:0.1rem 0.2rem;
        border-radius: 5px;
        position: absolute;
        right:0rem;
        bottom:0.8rem;
    }
    .gif{
      width:100%;
      text-align: center;
    }
    .tip{
      margin-bottom:0.5rem;
      text-align:center;
      font-size: 0.3rem;
    }
</style>



