<template>
    <div class="address">
        <dl class="reset-dl">
            <dt>定位城市</dt>
            <dd>
                <span   @click="$router.go(-1)">正在定位……</span>    
            </dd>
        </dl>
        <dl class="reset-dl">
            <dt>最近访问城市</dt>
            <dd>
                <!-- <router-link to=""  @click="$router.go(-1)">哈尔滨</router-link> -->
                <span   @click="$router.go(-1)">哈尔滨</span>    
                <span   @click="$router.go(-1)">北京</span>    
                <span   @click="$router.go(-1)">上海</span> 
            </dd>
        </dl>
        
        <dl class="reset-dl">
            <dt>热门城市</dt>
            <dd>  
                <span  @click="$router.go(-1)">广州</span>    
                <span  @click="$router.go(-1)">北京</span>    
                <span  @click="$router.go(-1)">上海</span>
                <span  @click="$router.go(-1)">深圳</span>    
                <span  @click="$router.go(-1)">武汉</span>    
                <span  @click="$router.go(-1)">天津</span> 
                <span  @click="$router.go(-1)">西安</span>    
                <span  @click="$router.go(-1)">杭州</span>    
                <span  @click="$router.go(-1)">南京</span>     
                <span  @click="$router.go(-1)">重庆</span>     
                <span  @click="$router.go(-1)">成都</span>   
            </dd>
        </dl>

        <ul class="city">
            <p>手动选择</p>
            <li v-for="city in address" :key="city.n">
                <div @click="$router.go(-1)">{{city.n}}</div>
                <!-- <div id="qu"></div> -->
            </li>
        </ul>
    </div>

</template>

<script>
import Axios from 'axios'
    export default{
        data(){
            return{
                address:[],
            }
        },
        
        mounted(){
            let url1='./static/data/city.json'
            Axios.get(url1).then(res=>{
                this.address=res.data.city;
            }).catch(
                ()=>{alert("数据库连接失败")}
            )
        }
    }
</script>

<style>
    .address{
        
        width:100% ;
        background: #EBEBEB;
    }
    .reset-dl{
        width: 100%;
    }
    .reset-dl dt{
        font:0.28rem/0.7rem "微软雅黑";
        color:#333333;
        margin: 0 0 0 0.4rem;

    }
    .reset-dl dd{
        background: #F5F5F5;
        padding: 0.2rem 0 0 0.2rem;
    }
    .reset-dl dd span{
        border:1px solid #E6E6E6;
        padding: 0.08rem 0.4rem;
        border-radius:0.05rem;
        color:#333333;
        background: #fff;
        margin: 0rem 0 0.2rem 0.2rem;
        display: inline-block;
    }
    .city p{
        font:0.28rem/0.7rem "微软雅黑";
        color:#333333;
        margin: 0 0 0 0.4rem;
    }
    .city li{
        border-bottom:1px solid #C8C7CC;
        font:0.28rem/0.8rem "微软雅黑";
        color:#333333;
        background: #F5F5F5;
        padding-left: 6%;
    }
</style>



