<template>
    <div class="details-movie">
        <section class="intro">
            <dl>
                <dt><img :src="movieDetails.img" alt=""></dt>
                <dd>
                    <h5>{{movieDetails.nm}}</h5>
                    <p>A Coll Fish </p>
                    <p>
                        <img src="../../assets/img/star.png" alt="">
                        <img src="../../assets/img/star.png" alt="">
                        <img src="../../assets/img/star.png" alt="">
                        <img src="../../assets/img/star.png" alt="">
                        <img src="../../assets/img/star2.png" alt="">
                        <span>{{movieDetails.sc}}</span></p>
                    <p>({{movieDetails.snum}}人评分)</p>
                    <p>{{movieDetails.cat}}</p>
                    <p>{{movieDetails.src}}/{{movieDetails.dur}}分钟</p>
                    <p>{{movieDetails.rt}}</p>
                </dd>
            </dl>
        </section>

        <section class="buy sec-margin-top">
            <router-link to="/details/detailscinema">特惠购票</router-link>
            <p>{{movieDetails.dra}}</p>
            <div><span v-show="show">&gt;</span></div>
        </section>

        <section class="sec-margin-top" id="star">
            <h5>演职人员</h5>
            <section class=" actor">
                <ul>
                    <li v-for="peo in star" :key="peo.id">
                        <img :src="peo.img" alt="">
                        <p>{{peo.star}}</p>
                        <p>{{peo.peop}}</p>
                    </li>
                </ul>
            </section>
        </section>

        <section class="appraise sec-margin-top">
            <h5>短评</h5>
            <ul>
                <li v-for="short in appraise" :key="short.id">
                    <div class="fen">
                        <img src="../../assets/img/star.png" alt="">
                        <img src="../../assets/img/star.png" alt="">
                        <img src="../../assets/img/star.png" alt="">
                        <img src="../../assets/img/star.png" alt="">
                        <img src="../../assets/img/star.png" alt="">
                        {{short.date}}
                    </div>
                    <p>{{short.pingjia}}</p>
                    <div class="dp_foot">
                        <div class="foot-left">
                            <img :src="short.photo" alt="">
                            <span>{{short.nm}}</span>
                            <em>购</em>
                        </div>
                        <div class="foot-right">
                            <img src="../../assets/img/zan.jpg" alt="">
                            <span>{{short.zan}}</span>
                            <img src="../../assets/img/pinglun.jpg" alt="">
                            <span>{{short.pinglun}}</span>
                        </div>
                    </div>
                </li>
            </ul>
            <div class='gif' v-show="loadingShow">
                加载更多<img src="../../assets/img/loading.gif" alt="">
            </div>
            <div class='tip' v-show="tip">
                <p>--------- 全部加载完成 ---------</p>
            </div>
        </section>

        <section class="sec-margin-top other">
            <h5>相关电影</h5>
            <section class="actor">
                <ul>
                    <li v-for="movie in movieList" :key="movie.id">
                        <img :src="movie.img" alt="">
                        <p>{{movie.nm}}</p>
                    </li>
                </ul>
            </section>
        </section>

        <section class="main_foot">
            <router-link to="">下载猫眼电影，查看更多电影信息</router-link>
            <p>© 猫眼电影 客服电话: <router-link to="">1010-5335</router-link></p>
            <p><span>京ICP备xxxxxx</span><span>京公网安备xxxxxxxx号</span></p>
            <p>北京猫眼文化传媒有限公司</p>
        </section>
    </div>
</template>
<script>
import Axios from 'axios'
export default{
    data(){
        return{
            movieDetails:[],
            star:[],
            appraise:[],
            movieList:[],
            show:true,/*p电影简介 */
            loadingShow:false,
            tip:false,

        };
    },
    methods:{
         goForward(movieId){
             this.$router.push('/details/DetailsMovie/'+movieId);
        },
        loaDate(){
             /*url3 */
        // Axios.get(url3)
        // .then(res=>{
        //     this.appraise = res.data.data.appraise;
        //     console.log(this.appraise)
        // }).catch(
        //     ()=>{alert("数据库连接失败")}
        // );
            let url3 = './static/data/movieappraise.json'
         Axios.get(url3).then(res=>{
            let appraise = res.data.data.appraise;
            let data=appraise.slice(this.appraise.length,this.appraise.length+3);
            if(data.length<3){
                this.tip=true;
            }
             this.appraise=this.appraise.concat(data);
            }).catch(
                ()=>{alert('数据库连接失败')}
                );   
        }
    },
    mounted(){

        
        //按照id获取地址
        let url1="./static/data/moviedetail" + this.$route.params.movieCinemaId +".json";
        /* let url1 = '/static/data/moviedetail248075.json' */
        let url2 = './static/data/detailsmovie.json'
        let url4 = './static/data/moviedata.json'
        
        /* url1*/
        Axios.get(url1)
        .then(res=>{
            this.movieDetails = res.data.data.MovieDetailModel;
        }).catch(
            ()=>{alert("数据库连接失败")}
        );
        /*url2 */
        Axios.get(url2)
        .then(res=>{
            this.star = res.data.data.actor;
        }).catch(
            ()=>{alert("数据库连接失败")}
        );
        /*url4 */
        Axios.get(url4)
        .then(res=>{
            this.movieList = res.data.data.movies;
        }).catch(
            ()=>{alert("数据库连接失败")}
        );
       
        /*部分加载*/
        this.loaDate();
        window.onscroll=()=>{
            let clientHeight= document.documentElement.clientHeight;//可视区域高度
            let scrollTop=document.documentElement.scrollTop;//滚动条的上方的像素点
            let scrollHeight=document.documentElement.scrollHeight;//能滚动高度
            if(clientHeight+scrollTop==scrollHeight){
                this.loadingShow=true;
                if(!this.tip){
                    this.loaDate();
                }else{
                    this.loadingShow=false;
                }
            }
        }

        let oMore=document.getElementsByClassName("buy")[0].getElementsByTagName("span")[0];
        let oP=document.getElementsByClassName("buy")[0].getElementsByTagName("p")[0];
        let num=0;
        oP.onclick =()=>{
                num++;
                if(num%2==1){
                    this.show=true;
                    oP.style.height = "100%";
                    oMore.style.bottom="-0.2rem";
                    oMore.style.transform="rotate(-90deg)";

                }else{
                    this.show=false;
                    oP.style.height = "3.5em";
                    oMore.style.position="absolute";
                    oMore.style.bottom="-0.3rem";
                }
                    
                
            
        }

    }
}
</script>
<style>
.header{
    position: fixed;
    top: 0;
}
.details-movie{
    width: 100%;
    background:#F4F4F4;
    margin: 1rem 0;
}
.sec-margin-top{
    margin-bottom: 0.2rem;
    background: #FFFFFF;
}
.intro{
    width: 100%;
    background: url(../../assets/img/bg1.jpg) no-repeat;
    background-size:100%;
}
.intro dl{
    padding: 0.3rem;
    display:flex;
    position: relative;
    
}
.intro dl dt{
    width:35%;
    
}
.intro dl dd{
    padding-left:0.3rem;
}
.intro dl dd h5{
    font:0.3rem/0.5rem "微软雅黑";
    color:#FFFFFF;
}
.intro dl dd p{
    color:#E3DEDA;
    font:0.25rem/0.4rem "微软雅黑";
    font-weight: bold;
}
.intro dl dd p img{
    height: 0.3rem;
}
.intro dl dd p span{
    color:#FFCC00;
    font-size:0.36rem;
    margin-right: 0.3rem;
}
.intro dl dd div{
    position:absolute;
    right:0.3rem;
    bottom:1.6rem;
    color:#C9BDAE;
    font:0.4rem/0.4rem "宋体";
    font-weight: bold;
}
.buy{
    width: 94%;
    overflow: hidden;
    padding: 3% 3% 0;
    border-bottom:1px solid #E5E5E5;
    position: relative;
}
.buy a{
    width: 100%;
    text-align: center;
    background: #E54847;
    border-radius: 0.1rem;
    color:#FFFFFF;
    display: inline-block;
    font:0.32rem/0.65rem "微软雅黑";
}
.buy p{
    padding:0.2rem 0.1rem;
    color:#555;
    font:0.25rem/0.35rem "微软雅黑";
    overflow: hidden;
    height: 2em;
    text-overflow: ellipsis;
}
.buy div{
    color: #AAAAAA;
    height: 0.25rem;
    font: 0.35rem/0.25rem "宋体";
    font-weight: bold;
    width: 100%;
    text-align: center;
}
.buy div span{
    display: inline-block;
    transform: rotate(90deg);
}
/*演职人员*/
#star{
    border-top: 1px solid #E5E5E5;
    border-bottom: 1px solid #E5E5E5;
}
#star h5,.other h5{
    padding-left: 0.2rem;
    font:0.25rem/0.7rem "微软雅黑";
    color:#747774;
    border-bottom:1px solid #e3e3e3;
    margin-bottom: 0.2rem;
}
/*演职人员*/
/*其他影片*/
.actor{
    width: 100%;
    margin: 0 auto;
    overflow: auto;
    flex-wrap: nowrap;
    justify-content: space-around;
    position: relative;
}
.actor ul{
    display: flex;
    width: 200%;
}
.other{
    margin-top: -0.2rem;
    padding-bottom: 0.2rem;
}
.other ul{
    width: 800%;
}
.other .actor ul li{
    width: 10%;
    margin: 0 0.1rem;
}
.other .actor ul li img{
    width: 100%;
    height: 2rem;
}
.other .actor ul li p{
    width:1.5rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.actor::-webkit-scrollbar{
    display: none;
}
.actor ul li{
    width: 10%;
    margin: 0 auto;
    text-align: center;
    color:#000000;
    font:0.23rem/0.4rem "微软雅黑";
}
.actor ul li p:nth-of-type(2){
    color:#B1B1B1;
}
/*短评.appraise*/
.appraise{
    width: 100%;
    border-top: 1px solid #E5E5E5;
}
.appraise h5{
    font:0.28rem/0.8rem "微软雅黑";
    border-bottom: 1px solid #E5E5E5;
    padding-left: 0.2rem;
    color:#666666;
    font-weight: bold;
}
.appraise ul{
    padding: 0.2rem;
}
.appraise ul li{
    border-bottom: 1px solid #E5E5E5;
    padding: 0.1rem ;
}
.appraise ul li .fen{
    color:#9999B3;
    font:0.23rem/0.3rem "微软雅黑";
    height: 0.4rem;
}
.appraise ul li .fen img{
    height: 60%;
}
.appraise ul li  p{
    color:#333;
    font:0.25rem/0.4rem "微软雅黑";
}
.appraise ul li .dp_foot{
    position: relative;
    height: 0.8rem;
    font: 0.23rem/0.85rem "微软雅黑";
    color:#999999;
}
.appraise ul li .dp_foot img{
    position: relative;
    top: 0.2rem;
}
.appraise ul li .foot-left{
    position:absolute;
    left: 0;
    
}
.appraise ul li .foot-left img{
    width: 0.5rem;
    height: 0.5rem;
    border-radius:50%; 
}
.appraise ul li .foot-left em{
    font-style: normal;
    color: #FFFFFF;
    background: #FF9900;
    padding: 0.03rem 0.07rem;
    margin-left:  0.1rem;
    border-radius: 0.05rem;
}
.appraise ul li .foot-right{
    position:absolute;
    right: 0;
}
.appraise ul li .foot-right img{
    margin-top:0.04rem; 
}
.gif{
   width:100%;
   text-align: center;
}
.tip{
    margin-bottom:0.5rem;
    text-align:center;
    font-size: 0.3rem;
}

/*main-footer*/
.main_foot{
    width: 100%;
    padding:2% 0 4%;
    text-align: center;
    background: #fff;
}
.main_foot>a{
    width: 92%;
    display: inline-block;
    font:0.3rem/0.7rem "微软雅黑";
    color:#FFFFFF;
    background: #E54847;
    border-radius: 0.05rem;
}
.main_foot p{
    font:0.22rem/0.55rem "微软雅黑";
    color:#9E9E9E;
}
.main_foot p:nth-of-type(1){
    color:#333;
}
.main_foot p:nth-of-type(1) a{
    color:#E33434;
    font-size: 0.23rem;
}
</style>



