<template>
    <div>
        <common-hback>
            <h1 slot="title">猫眼电影</h1>
        </common-hback>
        <div class="main">
            <div class="sear">
                <input type="text" placeholder="搜电影、搜影院" id="input">
                <img src="../../assets/img/search3.jpg" alt="">
                <span>取消</span>
            </div>
            <div class="hot-sear" @click="goBack">热门搜索</div>
        </div>
        <common-footer></common-footer>
    </div>
</template>

<script>
import commonHback from '../common/HeaderBack'
import commonFooter from '../common/Footer'
export default {
    components:{
        commonHback,
        commonFooter,
    },
    methods:{
        goBack(){
            this.$router.push('/')
        }
    },
    mounted(){
        let oOff=document.getElementsByClassName("sear")[0].getElementsByTagName("span")[0];
        let oInput=document.getElementById("input");
        oOff.onclick=()=>{
            oInput.value="";
        }
    }
}
</script>

<style>
    .main{
        position: fixed;
        top:1rem;
        width: 100%;
        height:100%;
        background: #F5F5F5;
    }
    .sear{
        width: 100%;
        height:1rem;
        border-bottom:1px solid #E5E5E5;
        position: relative;
    }
    .sear input{
        width:70%;
        height:0.53rem;
        font:0.25rem/0.53rem "微软雅黑";
        margin: 0.2rem;
        border-radius:0.05rem;
        padding-left:0.6rem;
        border:none;
        border:1px solid #E6E6E6;
    }
    .sear img{
        position: absolute;
        top:0.3rem;
        left:0.3rem;
    }
    .sear span{
        color:#F03D37;
        font:0.25rem/1rem;
    }
    .hot-sear{
        width: 100%;
        height:1.2rem;
        background: #fff;
        font:0.28rem/1.2rem "微软雅黑";
        color:#9A9A9A;
        padding: 0 4%;
    }
</style>



