        let oBanner = document.getElementsByClassName("banner")[0];
        let oNav = document.getElementById("secondary_nav");
        window.onscroll = ()=>{
            /*导航滚动过后定位在顶部*/
            if(document.documentElement.scrollTop>oNav.offsetHeight){
                oNav.style.position="fixed";
                oNav.style.top="1rem";
                oNav.style.left="0rem";

                for(let i=0;i<oLis.length;i++){
                oLis[i].onclick=()=>{
                    for(var j=0;j<oLis.length;j++){
                        oLis[j].style.color="#878787";
                        oNav1[j].style.display="none";
                    }
                    oLis[i].style.color="#f00";
                    oNav1[i].style.display="block";
                    /*商区、地铁站点击 */
                    for(let x=0;x<oNav2.length;x++){
                        oNav2[x].onclick = () =>{
                            for(let y=0;y<oNav2.length;y++){
                                oNav3[y].style.display="none";
                            }
                            oNav3[x].style.display="block";
                            for(let s=0;s<oNavQu.length;s++){
                                oNavQu[s].onclick = () =>{
                                    for(let m=0;m<oNavQu.length;m++){
                                        oNavLast[m].style.display="none";
                                    }
                                oNavLast[s].style.display="block";
                                }
                            }
                        }
                    }
                }
            }

            }else{
                oNav.style.position="relative";
                oNav.style.top="0";
                oNav.style.left="0";
               
                
            }  
        }

        /* 影院数据： */
        let url1 = './static/data/yingyuan.json'
         Axios.get(url1)
        .then(res=>{
            this.cinema = res.data.data;
            // console.log(this.cinema)
        }).catch(
            ()=>{alert("数据库连接失败")}
        )

