<template>
    <div>
        <common-hback>
            <h1 slot="title">资讯</h1>
        </common-hback>
        <main class="main-fir-details">
            <dl>
                <dt>
                    <h2>{{firstZixun[0].tit}}</h2>
                    <p>
                        <span>猫眼娱乐 {{firstZixun[0].time}}</span>
                        <span>阅读{{firstZixun[0].read}}</span>
                    </p>
                </dt>
                <dd>
                    <p>{{firstZixun[0].body1}}</p>
                    <img :src="firstZixun[0].img1" alt="">
                    <p>{{firstZixun[0].body2}}</p>
                    <img :src="firstZixun[0].img2" alt="">
                    <p>{{firstZixun[0].body3}}</p>
                    <img :src="firstZixun[0].img3" alt="">
                    <p>{{firstZixun[0].body4}}</p>
                    <img :src="firstZixun[0].img4" alt="">
                    <p>{{firstZixun[0].body5}}</p>
                    <img :src="firstZixun[0].img5" alt="">
                </dd>
            </dl>
            <dl class="share">
                <dt>
                    <img src="../../assets/img/good.jpg" alt="">
                    <p>——————·  分享  ·——————</p>
                </dt>
                <dd>
                    <ul>
                        <li><img src="../../assets/img/share5.jpg" alt=""><p>微信好友</p></li>
                        <li><img src="../../assets/img/share1.jpg" alt=""><p>朋友圈</p></li>
                        <li><img src="../../assets/img/share2.jpg" alt=""><p>QQ</p></li>
                        <li><img src="../../assets/img/share3.jpg" alt=""><p>微博</p></li>
                        <li><img src="../../assets/img/share4.jpg" alt=""><p>更多</p></li>
                    </ul>
                </dd>
            </dl>
        </main>
        <footer class="footer-fir-details">
            <input type="text" placeholder="写评论">
            <img src="../../assets/img/pinglun.jpg" alt="">
        </footer>
    </div>
</template>

<script>
import commonHback from '../common/HeaderBack'
import Axios from 'axios'
    export default{
        data(){
            return{
                firstZixun:[],
            }
        },
        components:{
             commonHback,
        },
        mounted(){
            let url1 = './static/data/information.json'
            Axios.get(url1).then(res=>{
                this.firstZixun = res.data.info;
                // console.log(this.firstZixun)
            }).catch(
                ()=>{alert("数据库连接失败")}
            )
        }

    }
</script>

<style>
header{
    position: fixed;
    top:0;
    left:0;
}
    .main-fir-details{
        padding: 0.2rem 0.3rem;
        margin: 1rem 0;
    }
    .main-fir-details dl dt h2{
        font:0.37rem/0.65rem "微软雅黑";
        font-weight: bold;
    }
    .main-fir-details dl dt p{
        font:0.23rem/0.5rem "微软雅黑";
        color:#888;
        position:relative;
        height:0.5rem;
        margin-bottom: 0.2rem;
    }
    .main-fir-details dl dt p span{
        position: absolute;
    }
    .main-fir-details dl dt p span:nth-of-type(2){
        right:0rem;
        top: 0;
    }
    .main-fir-details dl dd p{
        font:0.25rem/0.4rem "微软雅黑";
        color:#333;
        text-align: justify;
    }
    .main-fir-details dl dd img{
        width: 100%;
        margin: 0.2rem 0;
    }
    .share dt{ 
        width: 100%;
        text-align: center;
        font:0.25rem/0.5rem "微软雅黑";
        color:#888;
    }
    .share dt img{
        width: 1rem;
    }
    .share dt{
        margin: 0.3rem 0 0;
    }
    .share dt p{
        position: relative;
    }
    .share dd ul{
        display: flex;
        justify-content: space-around;
    }
    .share dd ul li{
        text-align: center;
    }
    .share dd ul li img{
        width:0.5rem;
        height:0.5rem;
        border:1px solid #999;
        border-radius: 0.1rem;
        padding: 0.1rem;
    }
    .share dd ul li p{
        font:0.22rem/0.25rem "微软雅黑";
        text-align: center;
    }
    .footer-fir-details{
        width: 100%;
        height: 0.8rem;
        position: fixed;
        bottom:0;
        left:0;
        background: #FFFFFF;
        border-top:1px solid #F1F1F1;
    }
    .footer-fir-details input{
        height: 0.5rem;
        width: 70%;
        border:none;
        margin: 0.2rem 0.2rem 0.1rem;
        border:1px solid #F1F1F1;
        border-radius:0.1rem;
        padding: 0rem 0.3rem;
        font:0.25rem/0.5rem "微软雅黑";
    }
    .footer-fir-details img{
        width: 0.6rem;
        position: relative;
        top:0.2rem;
    }
</style>



