import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import First from '@/components/first/First'
import Movie from '@/components/movie/Movie'
import MovieList from '@/components/movie/MovieList'
import MovieListNow from '@/components/movie/MovieListNow'  /*list:正在放映*/
import MovieListFuture from '@/components/movie/MovieListFuture'  /*list:即将放映*/
import Details from '@/components/details/Details'
import DetailsCinema from '@/components/details/DetailsCinema'
import DetailsMovie from '@/components/details/DetailsMovie'
import Cinema from '@/components/cinema/Cinema'
import Show from '@/components/show/Show'
import My from '@/components/my/my'
import Addr from '@/components/others/Addr'
import Search from '@/components/others/Search'
import DetailsShow from '@/components/others/DetailsShow'
import FirstDetails from '@/components/others/FirstDetails'
// import CinemaDetails from '@/components/others/CinemaDetails'
import CinemaDetail from '@/components/others/CinemaDetail'

Vue.use(Router)

export default new Router({
  // mode:'history',//去掉url中的"#"
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:'/first'
    },{
      path:'/first',
      name:'First',
      component:First
    },{
      path:'/movie',
      name:'Movie',
      component:Movie,
      children:[{
        path:'movielist',
        name:'MovieList',
        component:MovieList,
        redirect:'/movie/movielist/movielistnow',
        children:[{
          path:'movielistnow',
          name:'MovieListNow',
          component:MovieListNow
        },{
          path:'movielistfuture',
          name:'MovieListFuture',
          component:MovieListFuture
        }]
      }]
    },{
      path:'/details',
      name:'Details',
      component:Details,
      redirect:'/details/detalscinema',
      children:[{
        path:'detailscinema/:movieId',//有id值
        // path:'detailscinema',
        name:'DetailsCinema',
        component:DetailsCinema
      },{
        path:'detailsmovie/:movieCinemaId',
        /* path:'detailsmovie', */
        name:'DetailsMovie',
        component:DetailsMovie
      }]
    },{
      path:'/cinema',
      name:'Cinema',
      component:Cinema
    },{
      path:'/show',
      name:'Show',
      component:Show
    },{
      path:'/my',
      name:'My',
      component:My
    },{//定位
      path:'/others/addr',
      name:'Addr',
      component:Addr
    },{//搜索
      path:'/others/search',
      name:'Search',
      component:Search
    },{
      path:'/others/detailsshow',
      name:'DetailsShow',
      component:DetailsShow
    },{
      path:'/others/firstdetails',
      name:'FirstDetails',
      component:FirstDetails
    },/* {
      path:'/others/cinemadetails',
      name:'CinemaDetails',
      component:CinemaDetails
    }, */
    {
      path:'/others/cinemadetail',/* /:addId */
      name:'CinemaDetail',
      component:CinemaDetail
    }
  ]
})



