<template>
    <div class="future-list">
        <h5>近期最受欢迎</h5>
        <section class="recent">
            <ul>
                <li v-for="movie in movieList" :key="movie.id" @click="goDetail">
                    <div class="list-img"><img :src="movie.src" alt=""><span>{{movie.want}}想看</span></div>
                    <div class="list-info">
                        <p>{{movie.nm}}</p>
                        <p>{{movie.date}}</p>
                    </div>
                </li>
            </ul>
        </section>

        <section class="specific-date"><!--具体日期-->
            <h5>12月30日</h5>
            <dl v-for="movie in movieList" :key="movie.id" @click="goDetail">
                <dt class="movie-img">
                    <img :src="movie.src" alt="">
                </dt>
                <dd class="movie-info">
                    <h5>{{movie.nm}}</h5>
                    <p><span>{{movie.want}}</span>人想看</p>
                    <p>主演：{{movie.star}}</p>
                    <p>{{movie.date}}上映</p>
                    <router-link to="">预售</router-link>
                </dd>
            </dl>
        </section>
         <div class='gif' v-show="loadingShow">
            <img src="../../assets/img/loading.gif" alt="">
        </div>
        <div class='tip' v-show="tip">
            <p>--------- 敬请期待，其他好剧 ---------</p>
        </div>
    </div>
</template>

<script>
import Axios from "axios";
export default {
    data(){
        return{
            movieList:[],
            // loadingShow:false,
            // tip:false
        };
    },
    methods:{
        //  loaDate(){
        //  let url1='./static/data/moviefuturedata.json'
        //  Axios.get(url1).then(res=>{
        //     let list=res.data.data.movies;
        //     let data=list.slice(this.movieList.length,this.movieList.length+3);
        //     if(data.length<3){
        //         this.tip=true;
        //     }
        //      this.movieList=this.movieList.concat(data);
        //      console.log(this.movieList);
        //     }).catch(
        //         ()=>{alert('moviefuture-数据错误')}
        //         );   
        // },
        goDetail(){
            this.$router.push('/details/detailscinema/'+248075);/*没有数据包*/ 
        }
    },
    mounted(){
        // this.loaDate();
        
          let url1='./static/data/moviefuturedata.json'
          Axios.get(url1).then(res=>{
             this.movieList=res.data.data.movies;
          }).catch(
              ()=>{alert("正在热映-数据库连接失败")}
          );



        // window.onscroll=()=>{
        //     let clientHeight= document.documentElement.clientHeight;//可视区域高度
        //     let scrollTop=document.documentElement.scrollTop;//滚动条的上方的像素点
        //     let scrollHeight=document.documentElement.scrollHeight;//能滚动高度
        //     if(clientHeight+scrollTop==scrollHeight){
        //         this.loadingShow=true;
        //         if(!this.tip){
        //             this.loaDate();
        //         }else{
        //             this.loadingShow=false;
        //         }
        //     }
        // }
    }
};
</script>
<style>
    .future-list{
        margin: 1.8rem 0 1rem;
        width: 100%;
        overflow: hidden;
    }
    ::-webkit-scrollbar{
        display: none;
    }
    .future-list  h5{
        font: 0.3rem/1rem "微软雅黑";
        color: #7B7B7B;
        margin-left: 3%;
    }
    .future-list .recent{
        width: 94%;
        padding-bottom: 0.5rem;
        margin-left: 3%;
        overflow:auto;
        border-bottom: 0.2rem solid #F5F5F5;
    }
    .future-list .recent ul{
        overflow: auto;
        width: 430%;
    }
    .future-list .recent ul li{
        float: left;
        margin-right: 0.3rem;
        width: 1.85rem;
    }
    .future-list .recent ul li .list-img{
        position: relative;
    }
    .future-list .recent ul li .list-img img{
        width: 100%;
        height: 2.4rem;
    }
    .future-list .recent ul li .list-img span{
        position: absolute;
        bottom: 0;
        left: 0;
        color:#FAAF00;
        font:0.28rem/1.8em "微软雅黑";
        font-weight: bold;
        text-align: center;
        display: inline-block;
        width: 100%;
    }
    .future-list .recent ul li .list-info p:nth-of-type(1){
        font: 0.3rem/0.5rem "微软雅黑";
        font-weight: bold;
        color:#222222;
    }
    .future-list .recent ul li .list-info p:nth-of-type(2){
        font:0.26rem/0.3rem "微软雅黑";
        color:#ACACAC;
    }

    /*具体日期section*/
    .specific-date{
        width: 100%;
        overflow: hidden;

    }
    .specific-date dl{
        padding:0.2rem 0.3rem;
        display: flex;
    }
    .specific-date dl .movie-img{
        width: 25%;
    }
    .specific-date dl .movie-img img{
        width: 100%;
        height: 2.2rem;
    }
    .specific-date dl .movie-info{
        width: 70%;
        border-bottom: 1px solid #F0F0F0;
        font:0.2rem/0.35rem "微软雅黑";
        padding-left:0.3rem;
        position: relative;
    }
    .specific-date dl .movie-info h5{
        font:0.33rem/1.5em "微软雅黑";
        font-weight: bold;
        color:#333333;
        width:70%;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        margin-left: 0;
    }
    .specific-date dl .movie-info p{
        font:0.25rem/0.48rem "微软雅黑";
        color:#666666;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 75%;

    }
    .specific-date dl .movie-info p span{
        font-weight: bold;
        color:#FAAF00;
        font-size: 0.3rem;
    }
    .specific-date dl .movie-info a{
        font-size: 0.25rem;
        background: #3C9FE6;
        color:#FFFFFF;
        padding:0.1rem 0.2rem;
        border-radius: 5px;
        position: absolute;
        right:0rem;
        bottom:0.8rem;
    }
    .gif{
      width:100%;
      text-align: center;
    }
    .tip{
      margin-bottom:0.5rem;
      text-align:center;
      font-size: 0.3rem;
    }
</style>


